import React, { useState, useEffect } from "react";
import Container from "Components/Container";
import Header from "Components/Header";
import StoreLink from "Components/Fin/StoreLink";
import CocktailCard from "Components/Fin/CocktailCard";
import RankingCard from "Components/Fin/RankingCard";
import store from "Redux/store";
import Loading from "Components/Loading";
import AboutMojito from "Components/Fin/AboutMojito";
import GetCRanking from "Cocktail/GetRanking";
import GetERanking from "Emoji/GetRanking";

const Fin = () => {
  document.getElementsByTagName("body")[0].style.height = "auto";
  document.getElementsByTagName("html")[0].style.height = "auto";
  document.getElementsByTagName("body")[0].style.overflow = "visible";
  document.getElementsByTagName("html")[0].style.overflow = "visible";
  document.getElementById("root").style.height = "auto";
  const [all, SetAll] = useState(null);
  const GetAll = async () => {
    const cocktailObj = store.getState().cocktail;
    const cocktailRank = await GetCRanking();
    const emojiRank = await GetERanking();
    console.log(cocktailRank);
    SetAll([cocktailObj, cocktailRank, emojiRank]);
  };
  if (all === null) {
    GetAll();
  }
  const [stage, setStage] = useState(false);
  const changeStage = () => {
    if (stage) {
      setStage(false);
    } else {
      setStage(true);
    }
  };
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [stage]);
  return all === null ? (
    <Loading></Loading>
  ) : stage ? (
    <Container>
      <Header fin={true}></Header>
      <AboutMojito changeStage={changeStage}></AboutMojito>
    </Container>
  ) : (
    <Container>
      <Header fin={true}></Header>
      <CocktailCard cocktailObj={all[0]}></CocktailCard>
      <RankingCard
        rankingArr={all[2]}
        title="칵테일에 들어간 감정"
      ></RankingCard>
      <RankingCard rankingArr={all[1]} title="많이 받아간 칵테일"></RankingCard>
      {/* <RankingCard rankingArr={all[1]} title="많이 받아간 칵테일"></RankingCard> */}
      <StoreLink changeStage={changeStage}></StoreLink>
    </Container>
  );
};

export default Fin;
