import React from "react";
import styled from "styled-components";

const Box = styled.div`
  width: 100%;
  max-width: 500px;
  height: ${(props) => (props.text ? "130px" : "90px")};
  position: fixed;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const SmallBox = styled.div`
  height: ${(props) => (props.text ? "65px" : "90px")};
  margin-top: ${(props) => (props.text ? "10px" : "0px")};
  width: calc(100% - 30px);
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: ${(props) => (props.text ? "20px" : "15px")};
`;

const Numbers = styled.a`
  color: rgba(255, 255, 255, 0.4);
`;

const Footer = ({ children, text, href }) => {
  return (
    <Box text={text}>
      <SmallBox text={text}>{children}</SmallBox>
      {text ? (
        <Numbers className="dD" href={href ? href : null} target="_blank">
          {text}
        </Numbers>
      ) : null}
    </Box>
  );
};

export default Footer;
