import React from "react";
import styled from "styled-components";
import RankingItem from "Components/Fin/RankingItem";

const Container = styled.div`
  width: 100%;
  background-color: rgba(30, 30, 30, 1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 20px;
  margin-top: 10px;
`;

const TopBox = styled.div`
  width: 100%;
  padding-top: 10px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.5);
  font-family: "GmarketSans";
  font-weight: 300;
`;

const BottomBox = styled.div`
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 15px;
`;

const RankingCard = ({ rankingArr, title }) => {
  return (
    <Container className="dD">
      <TopBox>{title}</TopBox>
      <BottomBox>
        {rankingArr.map((rank, index) => {
          return rank.count > 0 ? (
            <RankingItem
              key={index}
              percentage={rank.percentage}
              count={rank.count}
              name={rank.name}
              color={rank.color}
              type={rank.type}
            ></RankingItem>
          ) : null;
        })}
      </BottomBox>
    </Container>
  );
};

export default RankingCard;
