import React, { useState } from "react";
import Container from "Components/Container";
import Footer from "Components/Footer";
import Header from "Components/Header";
import Start from "Components/Intro/Start";
import Adieu from "Components/Intro/Adieu";
import { dbService } from "fbase";

const Intro = () => {
  const [cocktailCount, setCocktailCount] = useState(null);
  const getCount = async () => {
    const cCount = await dbService
      .collection("cocktails")
      .doc("cocktail")
      .get();
    setCocktailCount(cCount.data().count);
  };
  if (cocktailCount === null) {
    getCount();
  }
  return (
    <Container fin={false}>
      <Header></Header>
      <Adieu></Adieu>
      <Footer
        text={
          cocktailCount === null
            ? "어서오세요!"
            : `지금까지 ${cocktailCount}명이 칵테일을 받아갔어요!`
        }
      >
        <Start></Start>
      </Footer>
    </Container>
  );
};

export default Intro;
