import EmojiList from "Emoji/EmojiList";
import store from "Redux/store";

const GetRanking = async () => {
  const emojiObj = store.getState().emoji;
  const { red, orange, yellow, blue, purple, green, pink, grey } = emojiObj;
  const count = red + orange + yellow + blue + purple + green + pink + grey;
  const emojiArr = [];
  for (const name in emojiObj) {
    emojiArr.push({
      name: name,
      count: emojiObj[name],
      percentage: (emojiObj[name] / count) * 100,
      color: EmojiList[name][2],
    });
  }
  emojiArr.sort(function (a, b) {
    return b.count - a.count;
  });
  return new Promise(function (resolve, reject) {
    resolve(emojiArr);
  });
};

export default GetRanking;
