import CocktailList from "Cocktail/Cocktail";
import { dbService } from "fbase";

const GetRanking = async () => {
  const cRanking = await dbService
    .collection("cocktails")
    .doc("cocktail")
    .get();
  const rankingArr = [];
  let all = 0;
  const rankingObj = cRanking.data().ranking;
  for (const key in rankingObj) {
    all = all + rankingObj[key];
  }
  for (const key in rankingObj) {
    rankingArr.push({
      name: CocktailList[key - 1].name,
      count: rankingObj[key],
      percentage: (rankingObj[key] / all) * 100,
      color: CocktailList[key - 1].color,
      type: CocktailList[key - 1].type,
    });
  }
  rankingArr.sort(function (a, b) {
    return b.count - a.count;
  });
  return new Promise(function (resolve, reject) {
    resolve(rankingArr);
  });
};

export default GetRanking;
