import React from "react";
import styled from "styled-components";
import EmojiList from "Emoji/EmojiList";
import CocktailList from "Cocktail/CocktailList";

const RankingContainer = styled.div`
  width: 100%;
  height: 70px;
  display: flex;
`;

const RankingBox = styled.div`
  width: 70px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`;

const RankingImg = styled.img`
  height: 40px;
  z-index: 1000;
`;

const RankingBG = styled.div`
  width: 26px;
  height: 36px;
  bottom: 15px;
  position: absolute;
  background-color: ${(props) =>
    props.color ? `#${props.color}` : "rgba(30, 30, 30, 1)"};
`;

const Pct = styled.span`
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
`;

const GraphBox = styled.div`
  width: calc(100% - 70px);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const GraphCount = styled.div`
  width: calc(100% - 15px);
  height: 20px;
  display: flex;
  align-items: flex-end;
  padding-bottom: 3px;
`;

const GraphBar = styled.div`
  width: calc(100% - 15px);
  margin-top: 5px;
  background-color: rgba(42, 41, 41, 1);
  height: 10px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
`;

const GraphColor = styled.div`
  position: absolute;
  height: 100%;
  width: ${(props) =>
    props.percentage ? `${props.percentage.toString()}%` : "0%"};
  background-color: ${(props) =>
    props.percentage ? `#${props.color}` : "white"};
`;

const RankingItem = ({ count, percentage, name, color, type }) => {
  const rankingUrl = type ? CocktailList[`type${type}g`] : EmojiList[name][0];
  const rankingName = type ? name : EmojiList[name][1];
  const percentageMath = Math.round(percentage);
  return (
    <RankingContainer>
      <RankingBox>
        <RankingImg src={rankingUrl}></RankingImg>
        {type ? <RankingBG color={color}></RankingBG> : null}
      </RankingBox>
      <GraphBox>
        <GraphCount>
          {rankingName} {count}개<Pct>({percentageMath}%)</Pct>
        </GraphCount>
        <GraphBar>
          <GraphColor percentage={percentage} color={color}></GraphColor>
        </GraphBar>
      </GraphBox>
    </RankingContainer>
  );
};

export default RankingItem;
