import React from "react";
import styled from "styled-components";
import EmojiList from "Emoji/EmojiList";
import store from "Redux/store";
import useEmoji from "Redux/useEmoji";

const EmojiBox = styled.div`
  border-radius: 15px;
  display: flex;
  align-items: center;
  font-weight: 700;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 1);
  color: rgba(18, 18, 18, 1);
  overflow: scroll;
`;

const EmojiItem = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  &:first-child {
    margin-left: 20px;
  }
  &:nth-child(8) {
    margin-right: 0px;
  }
`;

const EmojiImg = styled.img`
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
`;

const EmojiName = styled.div`
  width: 40px;
  height: 12px;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: ${(props) => `#${props.color}`};
`;

const MarginRight = styled.div`
  min-width: 20px;
  height: 40px;
`;

const Emoji = () => {
  const emojiObj = useEmoji();
  const emojis = [
    EmojiList.red[0],
    EmojiList.orange[0],
    EmojiList.yellow[0],
    EmojiList.blue[0],
    EmojiList.purple[0],
    EmojiList.green[0],
    EmojiList.pink[0],
    EmojiList.grey[0],
  ];
  const emojisName = [
    "red",
    "orange",
    "yellow",
    "blue",
    "purple",
    "green",
    "pink",
    "grey",
  ];
  const emojiClick = (e) => {
    const name = e.target.name;
    const recent = { recent: name };
    store.dispatch({
      type: "emoji",
      emoji: { ...emojiObj, [name]: emojiObj[name] + 1 },
    });
    store.dispatch({ type: "recent", recent: { ...recent, recent: name } });
  };

  return (
    <EmojiBox className="dD">
      {emojis.map((emoji, index) => {
        return (
          <EmojiItem key={index}>
            <EmojiImg
              src={emoji}
              onClick={emojiClick}
              name={emojisName[index]}
            ></EmojiImg>
            <EmojiName color={EmojiList[emojisName[index]][2]}>
              {EmojiList[emojisName[index]][1]}
            </EmojiName>
          </EmojiItem>
        );
      })}
      <MarginRight></MarginRight>
    </EmojiBox>
  );
};

export default Emoji;
